import { createBrowserRouter, RouterProvider } from "react-router-dom";
import "react-toastify/dist/ReactToastify.css";
import "./assets/icons/feather.css";
import "./index.css";
import "./pages/dashboard/dashboard.css";
import { IsAuthenticated } from "./controllers";

// import Root from "./routes/root";
// import Contact from "./routes/contact";

import Error from "./pages/error/error";
import Root from "./pages";
import Dashboard from "./pages/dashboard";
import Login from "./pages/login";
import Main from "./pages/main";
import Sent from "./pages/sent";
import Documentation from "./pages/documentation";
import Billing from "./pages/billing";
import Help from "./pages/help";
import Configuration from "./pages/configuration";
import Keys from "./pages/keys";

const requireAuth = (element: any) => {
  const isAuthenticated = IsAuthenticated();
  const currentRoute: string = window.location.pathname;

  if (!isAuthenticated && currentRoute !== "/login" && currentRoute !== "/") {
    window.location.href = "/login";
    return null;
  }

  return element;
};

const App = () => {
  const router = createBrowserRouter([
    {
      path: "/",
      element: <Main />,
      errorElement: <Error />,
    },
    {
      path: "/Login",
      element: <Login />,
      errorElement: <Error />,
    },
    {
      path: "/",
      element: <Root />,
      errorElement: <Error />,
      children: [
        {
          path: "dashboard",
          element: requireAuth(<Dashboard />),
        },
        {
          path: "sent",
          element: requireAuth(<Sent />),
        },
        {
          path: "keys",
          element: requireAuth(<Keys />),
        },
        // {
        //   path: "documentation",
        //   element: requireAuth(<Documentation />),
        // },
        // {
        //   path: "billing",
        //   element: requireAuth(<Billing />),
        // },
        // {
        //   path: "help",
        //   element: requireAuth(<Help />),
        // },
        {
          path: "configuration",
          element: requireAuth(<Configuration />),
        },
      ],
    },
  ]);

  return <RouterProvider router={router} />;
};

export default App;
