import React, { useRef, useEffect } from "react";
import { Chart, registerables } from "chart.js";
Chart.register(...registerables);

interface LineChartProps {
  checkedApplications: any;
  checkedShortlisted: any;
  checkedOnHold: any;
};

const FakeData = (min: number, max: number, days: number = 30) => {
  let number;
  let monthlyData = [];
  min = Math.ceil(min);
  max = Math.floor(max);

  for (let i = 0; i < days; i++) {
    number = Math.floor(Math.random() * (max - min) + min);
    monthlyData.push(number);
  }

  return monthlyData;
};

const LineChart: React.FC<LineChartProps> = ({checkedApplications, checkedShortlisted, checkedOnHold }) => {
  const chartContainer = useRef<HTMLCanvasElement>(null);
  const chartInstanceRef = useRef<Chart | null>(null);

  useEffect(() => {
    if (chartContainer && chartContainer.current) {
      const chartContext = chartContainer.current.getContext("2d");

      if (chartContext) {
        if (chartInstanceRef.current) {
          chartInstanceRef.current.destroy();
        }

        const gradient = chartContext.createLinearGradient(0, 0, 0, 300);
        gradient.addColorStop(0, "rgba(75, 192, 192, 1)");
        gradient.addColorStop(1, "rgba(14, 22, 42, 0.0)");

        const gradient2 = chartContext.createLinearGradient(0, 0, 0, 300);
        gradient2.addColorStop(0, "#FF7DCBF2");
        gradient2.addColorStop(1, "rgba(14, 22, 42, 0.0)");

        const gradient3 = chartContext.createLinearGradient(0, 0, 0, 300);
        gradient3.addColorStop(0, "#FF6200");
        gradient3.addColorStop(1, "rgba(14, 22, 42, 0.0)");

        chartInstanceRef.current = new Chart(chartContext, {
          type: "line",
          data: {
            labels: [
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
            ],
            datasets: [
              {
                label: "Applications",
                data: [
                  10, 12, 10, 22, 44, 31, 36, 72, 50, 55, 42, 47, 18, 22, 35,
                  32, 11, 17, 22, 55, 80, 85, 72, 77, 78, 90, 81, 88, 70, 78,
                ],
                fill: true,
                backgroundColor: gradient2,
                pointBackgroundColor: "transparent",
                pointHoverBackgroundColor: gradient2,
                borderColor: "#FF7DCBF2",
                tension: 0.4,
                cubicInterpolationMode: "monotone",
                hidden: !checkedApplications,
              },
              {
                label: "Shortlisted",
                data: FakeData(40, 60),
                fill: true,
                backgroundColor: gradient,
                pointBackgroundColor: "transparent",
                pointHoverBackgroundColor: gradient,
                borderColor: "rgb(75, 192, 192)",
                tension: 0.4,
                cubicInterpolationMode: "monotone",
                hidden: !checkedShortlisted,
              },
              {
                label: "On-Hold",
                data: FakeData(30, 50),
                fill: true,
                backgroundColor: gradient3,
                pointBackgroundColor: "transparent",
                pointHoverBackgroundColor: gradient3,
                borderColor: "#FF6200",
                tension: 0.4,
                cubicInterpolationMode: "monotone",
                hidden: !checkedOnHold,
              },
            ],
          },
          options: {
            responsive: true,
            maintainAspectRatio: false,
            scales: {
              x: {
                border: {
                  display: false,
                },
                grid: {
                  display: false,
                },
                ticks: {
                  display: false,
                },
              },
              y: {
                border: {
                  display: false,
                },
                grid: {
                  color: "rgba(255, 255, 255, 0.1)",
                },
                ticks: {
                  font: {
                    size: 9,
                  },
                },
              },
            },
            plugins: {
              legend: {
                display: false,
              },
            },
            elements: {
              point: {
                radius: 10, // Set point radius
                borderWidth: 0, // Set point border width to 0
                hoverRadius: 10, // Set the radius of the points when hovered
                hoverBorderWidth: 2,
              },
            },
          },
        });
      }
    }
  }, [checkedApplications, checkedShortlisted, checkedOnHold]);

  return (
    <div style={{ width: "100%", height: "100%" }}>
      <canvas ref={chartContainer} />
    </div>
  );
};

export default LineChart;
