  const handleClickExpand = (event: any) => {
    let classes =
      event.target.parentNode.parentNode.parentNode.parentNode.classList;

    if (!classes.contains("list-item")) {
      return;
    }

    let isExpanded = classes.contains("expanded");

    if (isExpanded) {
      event.target.textContent = "+";
      return classes.remove("expanded");
    }

    event.target.textContent = "-";
    return classes.add("expanded");
  };

