import React, { useState } from "react";
import SmsListContainer from "../../components/smsListContainer";
import EmailListContainer from "../../components/emailListContainer";
import WhatsappListContainer from "../../components/whatsappListContainer";
import "./sent.css";

interface StyleSheet {
  [key: string]: React.CSSProperties;
}

interface MessagesProps {}

const Sent: React.FC<MessagesProps> = () => {
  const [indexSelected, SetIndexSelected] = useState(1);
  const [currentClass, SetCurrentClass] = useState("slideIn1");

  const handleClick = (event: any, index: number) => {
    event.preventDefault();

    if (index === 1) {
      if (indexSelected === 1) {
        SetCurrentClass("slideIn1");
        SetIndexSelected(index);
        return;
      }

      if (indexSelected === 2) {
        SetCurrentClass("slideOut");
        SetIndexSelected(index);
        return;
      }

      if (indexSelected === 3) {
        SetCurrentClass("slideOut3-1");
        SetIndexSelected(index);
        return;
      }

      SetCurrentClass("slideIn");
      SetIndexSelected(index);
      return;
    }

    if (index === 2) {
      if (indexSelected === 2) {
        SetCurrentClass("slideIn1");
        SetIndexSelected(index);
        return;
      }

      if (indexSelected === 3) {
        SetCurrentClass("slideOut");
        SetIndexSelected(index);
        return;
      }

      SetCurrentClass("slideIn");
      SetIndexSelected(index);
      return;
    }

    if (index === 3) {
      if (indexSelected === 3) {
        SetCurrentClass("slideIn1");
        SetIndexSelected(index);
        return;
      }

      if (indexSelected === 1) {
        SetCurrentClass("slideIn1-3 ");
        SetIndexSelected(index);
        return;
      }

      SetCurrentClass("slideIn");
      SetIndexSelected(index);
      return;
    }

    console.log(event);
  };

  return (
    <div style={styles.navStyle}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          width: "70vw",
          height: "100vh",
          backgroundColor: "#151C32F2",
        }}
      >
        <div
          style={{
            height: "8%",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            color: "#5e6a81",
            fontWeight: 500,
          }}
        >
          <span
            style={{
              letterSpacing: 2,
            }}
          >
            Segunda-Feira, 16 de Janeiro de 2023
          </span>
        </div>
        <div
          style={{
            height: "8%",
            width: "100%",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <div
            style={{
              display: "flex",
              width: "50%",
              backgroundColor: "#01091ff2",
              borderTopLeftRadius: "25px",
              borderBottomLeftRadius: "25px",
              borderTopRightRadius: "25px",
              borderBottomRightRadius: "25px",
            }}
          >
            <div
              style={{
                position: "absolute",
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "space-between",
                // backgroundColor:"#00FF0010",
                width: "35%",
              }}
            >
              <div
                onClick={(e) => {
                  handleClick(e, 1);
                }}
                className={indexSelected === 1 ? currentClass : ""}
                style={styles.selectTypeMenuBG}
              />
              <div
                onClick={(e) => {
                  handleClick(e, 2);
                }}
                className={indexSelected === 2 ? currentClass : ""}
                style={styles.selectTypeMenuBG}
              />
              <div
                onClick={(e) => {
                  handleClick(e, 3);
                }}
                className={indexSelected === 3 ? currentClass : ""}
                style={styles.selectTypeMenuBG}
              />
            </div>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "space-between",
                width: "100%",
              }}
            >
              <div style={styles.selectTypeMenu}>SMS</div>
              <div style={styles.selectTypeMenu}>EMAIL</div>
              <div style={styles.selectTypeMenu}>WHATSAPP</div>
            </div>
          </div>
        </div>
        <div
          style={{
            height: "84%",
            width: "100%",
            // backgroundColor:'blue',
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              height: "95%",
              width: "95%",
              backgroundColor: "#0e162a",
              borderRadius: "20px",
            }}
          >
            {indexSelected === 1 && <SmsListContainer />}
            {indexSelected === 2 && <EmailListContainer />}
            {indexSelected === 3 && <WhatsappListContainer />}
          </div>
        </div>
      </div>
    </div>
  );
};

const styles: StyleSheet = {
  selectTypeMenuBG: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "33.3%",
    padding: "21.5px 25px",
    borderTopLeftRadius: "25px",
    borderBottomLeftRadius: "25px",
    borderTopRightRadius: "25px",
    borderBottomRightRadius: "25px",
    color: "#FFF",
    fontWeight: 500,
    cursor: "pointer",
  },
  selectTypeMenu: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "33.3%",
    padding: "12px 25px",
    borderTopLeftRadius: "25px",
    borderBottomLeftRadius: "25px",
    borderTopRightRadius: "25px",
    borderBottomRightRadius: "25px",
    color: "#FFF",
    fontWeight: 500,
  },
  logoContainer: {
    height: "10vh",
    display: "flex",
    alignItems: "center",
  },
  navStyle: {
    listStyleType: "none",
    margin: 0,
    padding: 0,
    display: "flex",
    flexDirection: "row",
    width: "100%",
  },
};

export default Sent;
