import React, { useState } from "react";
import "./ToggleSwitch.css";

interface ToggleSwitchProps {
  activatedColor: string;
  state: any;
  setState: any;
}

const ToggleSwitch: React.FC<ToggleSwitchProps> = ({ activatedColor, state, setState }) => {
  const onToggle = () => setState(!state);

  return (
    <label className="toggle-switch">
      <input type="checkbox" checked={state} onChange={onToggle} />
      <span
        className="switch"
        style={
          state
            ? {
                backgroundColor: activatedColor,
                border: `1px solid ${activatedColor}`,
              }
            : {}
        }
      />
    </label>
  );
};
export default ToggleSwitch;
