import React, { useEffect, useState } from "react";
import axios from "axios";
import { BASE_URL, AUTH } from "../../api/endpoints";
import "./login.css";

interface LoginProps {
}

const SocialContainer = () => {
  return (
    <div className="social-container">
      <a href="http://localhost:3000/login" className="social">
        <i className="fab fa-facebook-f"></i>
      </a>
      <a href="http://localhost:3000/login" className="social">
        <i className="fab fa-google-plus-g"></i>
      </a>
      <a href="http://localhost:3000/login" className="social">
        <i className="fab fa-linkedin-in"></i>
      </a>
    </div>
  );
};

const Login: React.FC<LoginProps> = ({ }) => {
  const [loginForm, setLoginForm]: any = useState({
    email: "",
    password: "",
  });

  const [registerForm, setRegisterForm]: any = useState({
    name: "",
    email: "",
    password: "",
  });

  const handleInputChangeLogin = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;

    setLoginForm((prevData: any) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmitLogin = (e: React.FormEvent) => {
    e.preventDefault();

    if(!loginForm.email && !loginForm.password){
      return
    }

    axios
      .post(BASE_URL + AUTH, loginForm)
      .then((response) => {
        localStorage.setItem("token", response?.data?.token);

        console.log(response);
        console.log("Login Form ! : ", loginForm);
        setLoginForm({
          email: "",
          password: "",
        });
      })
      .catch((error) => {
        if (error.response.status === 401) {

          const shakeContainer = document.getElementById("shake");

          if(!shakeContainer){
            return
          }

          shakeContainer.classList.add("ahashakeheartache");
          
          setTimeout(() => {
            shakeContainer.classList.remove("ahashakeheartache");
          }, 450)

          console.log("");
        } else {
          // Handle other errors
          console.log("An error occurred:", error.message);
        }
      });
  };

  const handleInputChangeRegister = (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const { name, value } = e.target;

    setRegisterForm((prevData: any) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmitRegister = (e: React.FormEvent) => {
    e.preventDefault();
    console.log("Register Form ! : ", registerForm);
    setRegisterForm({
      name: "",
      email: "",
      password: "",
    });
  };

  useEffect(() => {
    const signUpButton = document.getElementById("signUp");
    const signInButton = document.getElementById("signIn");
    const container = document.getElementById("container");

    if (!signUpButton || !signInButton || !container) {
      return;
    }

    signUpButton.addEventListener("click", () => {
      container.classList.add("right-panel-active");
    });

    signInButton.addEventListener("click", () => {
      container.classList.remove("right-panel-active");
    });
  }, []);

  return (
    <div className="login_page">
      <div id="shake">
        <div className="container" id="container">
          <div className="form-container sign-up-container">
            <form onSubmit={handleSubmitRegister}>
              <h1>Cadastrar</h1>
              <SocialContainer />
              <span>ou use seu email para cadastrar</span>
              <input
                type="text"
                name="name"
                value={registerForm.name}
                onChange={handleInputChangeRegister}
                placeholder="Nome"
              />
              <input
                type="email"
                name="email"
                value={registerForm.email}
                onChange={handleInputChangeRegister}
                placeholder="Email"
              />
              <input
                type="password"
                name="password"
                autoComplete="current-password"
                value={registerForm.password}
                onChange={handleInputChangeRegister}
                placeholder="Senha"
              />
              <button type="submit">Cadastrar</button>
            </form>
          </div>
          <div className="form-container sign-in-container">
            <form onSubmit={handleSubmitLogin}>
              <h1>Entrar</h1>
              <SocialContainer />
              <span>ou use seu cadastro</span>
              <input
                type="email"
                name="email"
                value={loginForm.email}
                onChange={handleInputChangeLogin}
                placeholder="Email"
              />
              <input
                type="password"
                name="password"
                autoComplete="current-password"
                value={loginForm.password}
                onChange={handleInputChangeLogin}
                placeholder="Senha"
              />
              <a href="http://localhost:3000/forgot">Esqueceu sua senha?</a>
              <button type="submit">Entrar</button>
            </form>
          </div>
          <div className="overlay-container">
            <div className="overlay">
              <div className="overlay-panel overlay-left">
                <h1>Já é cliente ?</h1>
                {/* <p>
                  To keep connected with us please login with your personal info
                </p> */}
                <p>Para iniciar sua sessão, preencha com seus dados.</p>
                <button className="ghost" id="signIn">
                  Entrar
                </button>
              </div>
              <div className="overlay-panel overlay-right">
                <h1>Novo por aqui ?</h1>
                {/* <p>Enter your personal details and start journey with us</p> */}
                <p>
                  Preencha com os seus dados para iniciar uma jornada com agente.
                </p>
                <button className="ghost" id="signUp">
                  Cadastrar
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
